import * as React from 'react'
import Tabs from '@mui/material/Tabs'
import Tab from '@mui/material/Tab'
import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'
import { AddConnection } from 'AddConnection'
import { DelConnection } from 'DelConnection'

interface TabPanelProps {
  children?: React.ReactNode
  index: number
  value: number
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && children}
    </div>
  )
}

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  }
}

export function App() {
  const [value, setValue] = React.useState(0)

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue)
  }

  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example" centered>
          <Tab label="接続一覧" {...a11yProps(0)} />
          <Tab label="接続登録" {...a11yProps(1)} />
          <Tab label="接続削除" {...a11yProps(2)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <Box m={3}>
          <Typography variant="h4">接続一覧を表示する</Typography>
          <Typography variant="subtitle1">・接続一覧から接続元装置を選択して登録/削除に遷移。</Typography>
          <Typography variant="subtitle1">・登録/削除タブで、接続元装置は固定。</Typography>
          <Typography variant="subtitle1">・登録/削除タブで、接続先装置を選択。</Typography>
        </Box>
      </TabPanel>
      <TabPanel value={value} index={1}>
        <AddConnection tabChange={setValue} />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <DelConnection tabChange={setValue} />
        {/* <Box m={3}>
          <Typography variant="h4">接続削除タブ</Typography>
          <Typography variant="subtitle1">・接続元装置は固定。接続先装置を選択する。</Typography>
          <Typography variant="subtitle1">
            ・接続先装置リストには、接続が存在する装置の選択リストを設定する。
          </Typography>
          <Typography variant="subtitle1">・削除する接続を選択する。</Typography>
          <Typography variant="subtitle1">
            ・もしくは削除の場合、接続元と接続先装置を両方固定⇒タブ切替遷移でも良いかも。
          </Typography>
        </Box> */}
      </TabPanel>
    </Box>
  )
}
